.parallax {
  height: 100vh;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.parallax__img {
  left: 50%;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100%;
  z-index: -1;
}

.parallax__content {
  margin-bottom: 0;
  position: relative;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}

.page-transition {
  animation: fade-in 0.2s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  background-color: #fff;
  color: #464a4c;
  height: 100vh;
  left: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  vertical-align: middle;
  visibility: visible;
  width: 100vw;
  z-index: 1050;
}

.page-transition .loader {
  animation: slide-in-left 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) 0.2s both;
  display: block;
  height: 6em;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.page-transition--showContent {
  animation: fade-out 0.2s cubic-bezier(0.55, 0.085, 0.68, 0.53) 0.5s both;
}

.page-transition--showContent .loader {
  animation: slide-out-right 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

@keyframes slide-out-right {
  0% {
    opacity: 1;
    transform: translateX(-50%);
    visibility: visible;
  }
  99% {
    opacity: 0;
    transform: translateX(calc(-50% + 70px));
    visibility: visible;
  }
  100% {
    opacity: 0;
    transform: translateX(calc(-50% + 70px));
    visibility: hidden;
  }
}

@keyframes slide-in-left {
  0% {
    opacity: 0;
    transform: translateX(calc(-50% - 70px));
    visibility: hidden;
  }
  1% {
    opacity: 0;
    transform: translateX(calc(-50% - 70px));
    visibility: visible;
  }
  100% {
    opacity: 1;
    transform: translateX(-50%);
    visibility: visible;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: translateZ(-80px);
    visibility: none;
  }
  1% {
    opacity: 0;
    transform: translateZ(-80px);
    visibility: visible;
  }
  100% {
    opacity: 1;
    transform: translateZ(0);
    visibility: visible;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
    transform: translateZ(0);
    visibility: visible;
  }
  99% {
    opacity: 0;
    transform: translateZ(-80px);
    visibility: visible;
  }
  100% {
    opacity: 0;
    transform: translateZ(-80px);
    visibility: hidden;
  }
}

